<template>
  <div class="brand-control-desktop grid">
    <div class="brand-control-desktop__header">
      <h2 class="name">{{ name }}</h2>
      <p class="descriptions">{{ descriptions }}</p>
      <router-link class="link no-mobile" :to="{ name: link.url }">{{ link.name }}</router-link>
    </div>
    <div class="brand-control-desktop__body">
      <div class="tags flex">
        <router-link tag="button" :to="{ name: item.link }" class="tags__item" v-for="(item, index) in tags" :key="index" @mouseenter.native="hover($event, item)" @mouseleave.native="hover($event, item)">{{ item.name }}</router-link>
      </div>
    </div>
    <div class="brand-control-desktop__footer">
      <button-link class="button" :link="button.url" :name="button.name" />
    </div>
  </div>
</template>

<script>

import ButtonLink from '@/components/core/ButtonLink'

export default {
  name: 'brand-control-desktop',
  props: {
    name: {
      type: String,
      default: ''
    },
    descriptions: {
      type: String,
      default: ''
    },
    button: {
      type: Object,
      default: () => ({
        name: 'button',
        url: 'error'
      })
    },
    tags: {
      type: Array,
      default: () => {}
    },
    link: {
      type: Object,
      default: () => ({
        name: 'link',
        url: 'error'
      })
    }
  },
  components: {
    ButtonLink
  },
  methods: {
    hover (event, key) {
      if (event.type === 'mouseleave') {
        this.$emit('mouse', null)
        return
      }
      this.$emit('mouse', key)
    }
  }
}
</script>

<style lang="scss" scoped>

