  }
  .descriptions {
    font-size: 16px;
    line-height: 24px;
    margin-bottom: 0;
  }
  .link {
    display: none;
    color: $link;
  }
  .tags {
    flex-wrap: wrap;
    &__item {
      transition: all .2s ease;
      outline: none;
      font-size: 14px;
      line-height: 20px;
      border-radius: 4px;
      background-color: $tag;
      height: 40px;
