<template>
  <div v-show="hiddenBigBanner" class="big-banner" ref="bigBanner" v-scroll="handleScroll">
    <agile class="big-banner__slider" @after-change="curentDot($event)" ref="carousel" :options="agile">
      <div class="slide" v-for="(index) in 5" :key="index">
        <banner-item :class="{ 'active': (currentSlide + 1) === index }"  v-if="(currentSlide + 1) === index"/>
      </div>
    </agile>
    <div class="big-banner__control">
      <div class="control-bar flex">
        <button class="control-bar__button left no-mobile" @click="prev">
          <v-icon>icon-pointer-left</v-icon>
        </button>
        <div class="control-bar__space flex">
          <div class="control-bar__dot" :class="{ 'active': currentSlide === 0 }"></div>
          <div class="control-bar__dot" :class="{ 'active': currentSlide > 0 &&  currentSlide < slider.length - 1 }"></div>
          <div class="control-bar__dot" :class="{ 'active': currentSlide === slider.length - 1 }"></div>
        </div>
        <button class="control-bar__button right no-mobile" @click="next">
          <v-icon>icon-pointer-right</v-icon>
        </button>
      </div>
    </div>
  </div>
</template>

<script>

import { VueAgile } from 'vue-agile'
import BannerItem from '@/components/banner/BannerItem'

export default {
  name: 'big-banner',
  data: () => ({
    hiddenBigBanner: true,
    bannerAnimationsStart: false,
    currentSlide: 0,
    agile: {
      navButtons: false,
      dots: false,
      fade: true
    },
    slider: [
      {
        name: '1'
      },
      {
        name: '1'
      },
      {
        name: '1'
      },
      {
        name: '1'
      },
      {
        name: '1'
      }
    ]
  }),
  components: {
    agile: VueAgile,
    BannerItem
  },
  methods: {
    curentDot (event) {
      this.currentSlide = event.currentSlide
    },
    next () {
      this.$refs.carousel.goToNext()
    },
    prev () {
      this.$refs.carousel.goToPrev()
    },
    handleScroll () {
      const slider = this.$refs.carousel.$el.querySelectorAll('.banner-item.active')[1]
      const bigBanner = this.$refs.bigBanner
      const textBox = slider.querySelector('.text-box')
      const circle = slider.querySelector('.circle')
      const square = slider.querySelector('.square')
      const image = slider.querySelector('.banner-item__image-blok')
      // const top = slider.getBoundingClientRect().top
      const top = document.getElementById('home').getBoundingClientRect().top - 72

      this.scrollHidden(bigBanner, Math.abs(top))
      // bigBanner.style.transform = `translateY(${Math.abs(top) * 1.2}px)`
      textBox.style.transform = `translateX(${top}px)`
      circle.style.transform = `translateX(${top / 1.5}px)`
      square.style.transform = `translateX(${Math.abs(top) * 1.8}px)`
      image.style.transform = `translateX(${Math.abs(top) * 1.2}px)`
    },
    scrollHidden (el, top) {
      const scrollCoef = 0.0015
      const opacity = 1 - top * scrollCoef
      el.style.opacity = opacity
      if (opacity <= 0) {
        this.hiddenBigBanner = false
      } else {
        this.hiddenBigBanner = true
      }
    }
  }
}
</script>

