.big-banner {
  background-color: $background-banner;
  width: 100vw;
  position: relative;
  position: fixed;
  z-index: 1;
  margin-top: -72px;
  &__control {
    position: absolute;
    bottom: 34px;
    left: 34px;
  }
  &__slider {
    .slide {
      height: 100vh;
    }
  }
}

.control-bar {
  &__button {
    outline: none;
    padding: 10px;
    border-radius: 10px;
    &:hover, &:focus {
      background-color: rgba(0, 0, 0, 0.1);
    }
    .v-icon {
      color: black;
      font-size: 24px;
    }
  }
  &__space {
    width: 76px;
    max-width: 76px;
    justify-content: space-between;
    align-items: center;
    margin: 0 14px;
  }
  &__dot {
    $size: 8px;
    width: $size;
    height: $size;
    background-color: $background-dot;
    border-radius: $size;
    margin: 2px;
    box-sizing: content-box;
    transition: background-color .2s ease;
    &.active {
      background-color: black;
      margin: 0;
      padding: 2px;
    }
  }
}
</style>
