<template>
  <div class="band-card">
    <div class="band-card__header grid" :class="{'reverse': reverse}">
      <div class="band-card__content gap">
        <brand-control-desktop :name="BrandControlDesktop.name" :descriptions="BrandControlDesktop.descriptions" :button="BrandControlDesktop.button" :tags="BrandControlDesktop.tags" :link="BrandControlDesktop.link" @mouse="hover($event)"/>
      </div>
      <div class="band-card__image">
        <transition name="default">
          <keep-alive v-if="showBigImage" key="current-gategory">
            <div class="position__absolute">
              <component v-bind:is="currentAnimation" :image="showBigImage.image" :start="animationStatus"></component>
            </div>
          </keep-alive>
          <brand-image class="band-card__image-item" key="default" v-else :src="BrandImage.src" :alt="BrandImage.alt"/>
        </transition>
      </div>
    </div>
    <div class="band-card__footer gap">
      <div class="tags">
        <tags-mobile :tags="BrandControlDesktop.tags" />
      </div>
      <div class="popular-products">
        <h4 class="popular-products__title">{{ popularProductsTitle }}</h4>
        <popular-products-slider class="products" />
      </div>
      <div class="button-link">
        <button-link class="button" :link="BrandControlDesktop.button.url" :name="BrandControlDesktop.button.name" />
      </div>
    </div>
  </div>
</template>

<script>

import BrandImage from '@/components/brand/BrandImage'
import BrandControlDesktop from '@/components/brand/BrandControlDesktop'
import TagsMobile from '@/components/brand/TagsMobile'
import PopularProductsSlider from '@/components/brand/PopularProductsSlider'
import ButtonLink from '@/components/core/ButtonLink'

import CardCircle from '@/components/brand/animation-card/Circle'
import CardCircle2 from '@/components/brand/animation-card/Circle2'
import CardEllipse from '@/components/brand/animation-card/Ellipse'
import Rhombus from '@/components/brand/animation-card/Rhombus'
import Square from '@/components/brand/animation-card/Square'
import Star from '@/components/brand/animation-card/Star'

export default {
  name: 'brand-card',
  props: {
    reverse: {
      type: Boolean,
      default: false
    }
  },
  data: () => ({
    animationStatus: false,
    BrandControlDesktop: {
      name: 'Converse',
      descriptions: 'Взяв свое начало с обычных баскетбольных площадок, кроссовки Converse ныне достигли культового статуса. ',
      button: {
        name: 'Все товары бренда',
        url: 'error'
      },
      link: {
        name: 'Все товары →',
        url: 'error'
      },
      tags: [
        {
          name: 'Высокие кеды',
          key: 'High-top sneakers',
          image: require('@/assets/brand/tags/Converse_Высокие-кеды.png'),
          alt: 'category image',
          link: 'error',
          animationType: 'Square',
          animationBackground: ''
        },
        {
          name: 'Низкие кеды',
          key: 'Low top sneakers',
          image: require('@/assets/brand/tags/Converse_Низкие кеды.png'),
          alt: 'category image',
          link: 'error',
          animationType: 'Star',
          animationBackground: ''
        },
        {
          name: 'На платформе',
          key: 'On the platform',
          image: require('@/assets/brand/tags/Converse_На-платформе.png'),
          alt: 'category image',
          link: 'error',
          animationType: 'Rhombus',
          animationBackground: ''
        },
        {
          name: 'Одежда',
          key: 'clothing',
          image: require('@/assets/brand/tags/Havaianas_Мокасины.png'),
          alt: 'category image',
          link: 'error',
          animationType: 'CardEllipse',
          animationBackground: ''
        },
        {
          name: 'Рюкзаки',
          key: 'Backpacks',
          image: require('@/assets/brand/tags/Converse_Рюкзаки.png'),
          alt: 'category image',
          link: 'error',
          animationType: 'CardCircle',
          animationBackground: ''
        },
        {
          name: 'Головные уборы',
          key: 'Hats',
          image: require('@/assets/brand/tags/Havaianas_Сандалии.png'),
          alt: 'category image',
          link: 'error',
          animationType: 'CardCircle2',
          animationBackground: ''
        }
      ]
    },
    BrandImage: {
      src: require('@/assets/brand/brand_img-01.jpg'),
      alt: 'image converse'
    },
    BrandImage2: {
      src: require('@/assets/brand/brand_img-02.jpg'),
      alt: 'image converse'
    },
    geometry: {
      image: require('@/assets/brand/geometry/box.svg'),
      alt: 'box'
    },
    popularProductsTitle: 'Популярные товары',
    showBigImage: null
  }),
  components: {
    BrandImage,
    BrandControlDesktop,
    TagsMobile,
    PopularProductsSlider,
    ButtonLink,
    CardCircle,
    CardCircle2,
    CardEllipse,
    Rhombus,
    Star,
    Square
  },
  computed: {
    currentAnimation () {
      return this.showBigImage ? this.showBigImage.animationType : null
    }
  },
  methods: {
    hover (event) {
      if (event) {
        this.animationStatus = true
        this.showBigImage = event
        return
      }
      this.animationStatus = false
      this.showBigImage = event
    }
  }
}
</script>

