<template>
  <div class="subscription w-100">
    <h4 class="subscription__title">{{ title }}</h4>
    <p class="subscription__discription">{{ discription }}</p>
    <form class="subscription__form grid" @submit.prevent="submit">
      <input @blur="$v.email.$touch()" v-model="email" type="email" :placeholder="placeholder" class="subscription__input w-100">
      <button :disabled="$v.$invalid" type="submit" class="subscription__button" :class="{'disabled': $v.$invalid}">
        <v-icon>icon-arrow-top-right</v-icon>
      </button>
    </form>
    <span v-show="message" class="subscription__message">{{ message }}</span>
  </div>
</template>

<script>

import { required, email } from 'vuelidate/lib/validators'
import { validationMixin } from 'vuelidate'

export default {
  name: 'subscription',
  mixins: [validationMixin],
  data: () => ({
    title: 'Будь вкурсе событий',
    discription: 'Подпишись на самые свежие новости о новых коллекциях, акциях и многом другом',
    placeholder: 'Введите email',
    email: null
  }),
  validations () {
    return {
      email: {
        required,
        email
      }
    }
  },
  computed: {
    message () {
      if (this.$v.email.$dirty && !this.$v.email.required) {
        return 'Поля должно быть заполнено'
      }
      if (this.$v.email.$dirty && !this.$v.email.email) {
        return 'Email введён неправильно'
      }
      return null
    }
  },
  methods: {
    submit () {
      console.log('== submit')
    }
  }
}
</script>

