<template>
  <div class="location-modal-menu" @mouseleave="clos">
    <div class="location-modal-menu__close w-100">
      <button class="location-modal-menu__close__button" @click="clos">
        <v-icon>icon-clos</v-icon>
      </button>
    </div>
    <div class="location-modal-menu__container w-100">
      <input class="location-modal-menu__input w-100" v-model="locationUser" type="text" :placeholder="placeholder">
      <ul v-if="!locationUser" class="location-modal-menu__list">
        <li class="location-modal-menu__list-item" v-for="(item, index) in locationDefault" :key="index">
          <button class="location-modal-menu__location" @click="setLocation(item)">
            <span class="city">{{ item.city }}</span> <span class="region">{{ item.region }}</span>
          </button>
        </li>
      </ul>
      <ul v-else class="location-modal-menu__list">
        <li class="location-modal-menu__list-item" v-for="(item, index) in locationSearchList" :key="index">
          <button class="location-modal-menu__location" @click="setLocation(item)">
            <span class="city">{{ item.city }}</span> <span class="region">{{ item.region }}</span>
          </button>
        </li>
      </ul>
    </div>
  </div>
</template>

<script>
export default {
  name: 'location-modal-menu',
  data: () => ({
    placeholder: 'Ваш город',
    locationsList: require('@/assets/City.json'),
    locationUser: null,
    locationDefault: [
      {
        city: 'Москва',
        region: ''
      },
      {
        city: 'Санкт-Петербург',
        region: ''
      },
      {
        city: 'Краснодар',
        region: 'Краснодарсий край'
      },
      {
        city: 'Екатеринбург',
        region: 'Свердловская область'
      },
      {
        city: 'Новосибирск',
        region: 'Новосибирская область'
      },
      {
        city: 'Казань',
        region: 'Татарстан республика'
      }
    ]
  }),
  computed: {
    locationSearchList () {
      return this.locationsList.filter(item => {
        if (item.city.toLowerCase().includes(this.locationUser.toLowerCase()) || item.region.toLowerCase().includes(this.locationUser.toLowerCase())) {
          return item
        }
      })
    }
  },
  methods: {
    clos () {
      this.$store.dispatch('closLocationModal')
    },
    setLocation (location) {
      this.$store.dispatch('setLocation', location.city)
      this.clos()
    }
  },
  mounted () {
    document.documentElement.style.overflow = 'hidden'
  },
  destroyed () {
    document.documentElement.style.overflow = 'auto'
  }
}
</script>

