<template>
  <header-carcass class="header" :show-menu="!!currentComponent" @mouseleave.native="close()">
    <template v-slot:open-button>
      <button-burger />
    </template>

    <template v-slot:logo>
      <app-logo />
    </template>

    <template v-slot:default>
      <div class="header__content flex">
        <nav class="header__content-left">
          <header-item v-for="(item, index) in catalog" :key="index" :name="item.name" @mouseenter.native="openMenu(item.value)" :class="{'sale': item.value === 'sale'}"/>
        </nav>
        <div class="header__content-right">
          <select-locations @click.native="locationModalOpen"/>
          <button-stores />
        </div>
      </div>
    </template>

    <template v-slot:tools>
      <div class="header__tools-group flex">
        <button-search @click.native="searchProductModalOpen" />
        <button-account />
        <button-cart />
      </div>
    </template>

    <template v-slot:menu>
      <div class="header__menu">
        <keep-alive>
          <component v-bind:is="currentComponent"></component>
        </keep-alive>
      </div>
    </template>
  </header-carcass>
</template>

<script>

import HeaderCarcass from '@/components/header/HeaderCarcass'
import AppLogo from '@/components/header/Logo'
import ButtonBurger from '@/components/header/ButtonBurger'
import ButtonSearch from '@/components/header/ButtonSearch'
import ButtonAccount from '@/components/header/ButtonAccount'
import ButtonCart from '@/components/header/ButtonCart'
import HeaderItem from '@/components/header/HeaderItem'
import SelectLocations from '@/components/header/SelectLocations'
import ButtonStores from '@/components/header/ButtonStores'

import Brand from '@/components/brand-bar/BrandBar'
import Kids from '@/components/menu/Kids'
import Mens from '@/components/menu/Mens'
import Womens from '@/components/menu/Womens'
import Sale from '@/components/menu/Sale'

export default {
  name: 'app-header',
  components: {
    HeaderCarcass,
    AppLogo,
    ButtonBurger,
    ButtonCart,
    ButtonAccount,
    ButtonSearch,
    HeaderItem,
    SelectLocations,
    ButtonStores,
    Brand,
    Kids,
    Mens,
    Womens,
    Sale
  },
  data: () => ({
    showMenu: false,
    currentComponent: null,
    catalog: [
      {
        name: 'Бренды',
        value: 'brand'
      },
      {
        name: 'Женское',
        value: 'womens'
      },
      {
        name: 'Мужское',
        value: 'mens'
      },
      {
        name: 'Детское',
        value: 'kids'
      },
      {
        name: 'Распродажа',
        value: 'sale'
      }
    ]
  }),
  methods: {
    openMenu (value) {
      this.currentComponent = value
    },
    close () {
      if (this.currentComponent) {
        this.currentComponent = null
      }
    },
    locationModalOpen () {
      this.$store.dispatch('openLocationModal')
    },
    searchProductModalOpen () {
      this.$store.dispatch('openSearchModal')
    }
  }
}
</script>

