@import '../../sass/grid';

$sale: #EE6F47;

.header {
  &__content {
    justify-content: space-between;
  }
  &__content-right {
    > * {
      margin-right: 16px;
      &:last-child {
        margin-right: initial;
      }
    }
  }
  &__tools-group {
    > * {
      margin-right: 16px;
      &:last-child {
        margin-right: initial;
      }
      @media #{$media-sm} {
        margin-right: initial;
      }
    }
  }
  &__menu {
    margin: 0 auto;
    max-width: 1216px;
    padding: 32px 0;
  }
}
.sale {
  color: $sale;
}
</style>
