<template>
  <div class="tags-mobile">
    <agile :options="agile">
      <div class="slide" v-for="(item, index) in tags" :key="index">
        <router-link tag="button" :to="{ name: item.link }" class="tags-mobile__item flex">
          <v-img :src="item.image" :alt="item.alt" />
          <span>{{ item.name }}</span>
        </router-link>
      </div>
    </agile>
  </div>
</template>

<script>

import { VueAgile } from 'vue-agile'

export default {
  name: 'tags-mobile',
  props: {
    tags: {
      type: Array,
      default: () => []
    }
  },
  components: {
    agile: VueAgile
  },
  data: () => ({
    agile: {
      navButtons: false,
      dots: false,
      slidesToShow: 2,
      infinite: true,
      responsive: [
        {
          breakpoint: 500,
          settings: {
            slidesToShow: 3
          }
        },
        {
          breakpoint: 700,
          settings: {
            slidesToShow: 4
          }
        }
      ]
    }
  }),
  methods: {
    hover (event, key) {
      if (event.type === 'mouseleave') {
        this.$emit('mouse', null)
        return
      }
      this.$emit('mouse', key)
    }
  }
}
</script>

<style lang="scss" scoped>

.tags-mobile {
  max-width: 85vw;
  margin: 0 auto;
  &__item {
    outline: none;
    flex-direction: column;
    width: 148px;
    height: 198px;
    align-items: center;
    .v-image {
      $size: 148px;
      border-radius: 8px;
      height: $size;
      width: $size;
      background-color: #F4F4F4;
    }
    span {
      padding: 15px 0;
      font-weight: 600;
      font-size: 14px;
      line-height: 20px;
    }
  }
}
