<template>
  <div class="banner-brand-bar flex w-100">
    <router-link class="banner-brand-bar__item" v-for="(item, index) in options" :key="index" :to="{ name: item.link }" @mouseleave.native="noFocus(item)" @mouseenter.native="focus(item)">
      <v-img :src="item.black" :alt="item.alt" class="banner-brand-bar__image black-white" />
      <v-img :src="item.color" :alt="item.alt" class="banner-brand-bar__image color" />
    </router-link>
  </div>
</template>

<script>
export default {
  name: 'banner-brand-bar',
  props: {
    options: {
      type: Array,
      default: () => []
    }
  },
  methods: {
    focus (brand) {
      this.$emit('focus', brand)
    },
    noFocus (brand) {
      this.$emit('no-focus', brand)
    }
  }
}
</script>

<style lang="scss" scoped>
.banner-brand-bar {
  padding: 20px 32px;
  background-color: rgba(255, 255, 255, 0.8);
  border-radius: 8px;
  justify-content: space-between;
  width: auto;
  &__item {
    width: 9.31vw;
    height: 4.44vw;
    position: relative;
    margin-right: 13px;
    &:last-child {
      margin-right: initial;
    }
    &:hover, &:focus {
      .color {
        opacity: 1;
      }
      .black-white {
        opacity: 0;
      }
    }
  }
  &__image {
    position: absolute;
    width: 9.31vw;
    height: 4.44vw;
  }
  .color {
    transition: opacity .5s ease-out;
    opacity: 0;
  }
  .black-white {
    transition: opacity .5s ease-in;
    opacity: 1;
  }
}
</style>
