import Vue from 'vue'
import Vuex from 'vuex'

Vue.use(Vuex)

export default new Vuex.Store({
  state: {
    location: {
      name: 'Москва'
    },
    ui: {
      locationModal: {
        open: false
      },
      searchModal: {
        open: false
      }
    },
    filteredCategories: []
  },
  getters: {
    getFilteredCategories: s => s.filteredCategories
  },
  mutations: {
    OPEN_LOCATION (state) {
      state.ui.locationModal.open = true
    },
    CLOS_LOCATION (state) {
      state.ui.locationModal.open = false
    },
    SET_LOCATION (state, payload) {
      state.location.name = payload
    },
    OPEN_SEARCH (state) {
      state.ui.searchModal.open = true
    },
    CLOSE_SEARCH (state) {
      state.ui.searchModal.open = false
    },
    UPDATE_CATEGORIES (state, title) {
      state.filteredCategories = title
    }
  },
  actions: {
    openLocationModal ({ commit }) {
      commit('OPEN_LOCATION')
    },
    closLocationModal ({ commit }) {
      commit('CLOS_LOCATION')
    },
    setLocation ({ commit }, payload) {
      commit('SET_LOCATION', payload)
    },
    openSearchModal ({ commit }) {
      commit('OPEN_SEARCH')
    },
    closeSearchModal ({ commit }) {
      commit('CLOSE_SEARCH')
    },
    updateCategories ({ commit }, title) {
      commit('UPDATE_CATEGORIES', title)
    }
  },
  modules: {
  }
})
